<template>
  <div class="Home">
    <div class="home-title-area">
      <div :class="['home-title-time', time.isDarkTheme ? 'dark-theme' : '']" @click="toggleTheme">
        <div class="date-section">
          <div class="year">{{ formatDate(time.currentTime).year }}</div>
          <div class="month">{{ formatDate(time.currentTime).month }}</div>
          <div class="day">{{ formatDate(time.currentTime).day }}</div>
          <div class="day-of-week">{{ formatDate(time.currentTime).dayOfWeek }}</div>
        </div>
        
        <div class="time-section">
          <div class="time-display">
            <div class="time-unit">
              <div class="time-value">{{ formatTime(time.currentTime).hours }}</div>
              <div class="time-label">时</div>
            </div>
            <div class="time-separator">:</div>
            <div class="time-unit">
              <div class="time-value">{{ formatTime(time.currentTime).minutes }}</div>
              <div class="time-label">分</div>
            </div>
            <div class="time-separator">:</div>
            <div class="time-unit">
              <div class="time-value">{{ formatTime(time.currentTime).seconds }}</div>
              <div class="time-label">秒</div>
            </div>
          </div>
        </div>
      </div>
      <div class="home-title-desc">
        <h3>Archer Tagaryen 个人主页</h3>
        <p>技术爱好者,全栈开发</p>
        <p>熟悉c/c++,java,javascript(nodejs),python,go等多种编程语言</p>
        <p>contact: tagaryen199@126.com</p>
      </div>
    </div>
    <div class="home-content-area">
      <div>
        <div class="home-content-line">
          <div class="home-app home-app-bing" @click="openUrl('https://cn.bing.com')">Bing</div>
          <div class="home-app home-app-google" @click="openUrl('https://www.google.com')">Google</div>
          <div class="home-app home-app-github home-app-right" @click="openUrl('https://github.com/tagaryen')">Github</div>
        </div>
        <div class="home-content-line">
          <div class="home-app home-app-json" @click="jsonSection">JSON美化</div>
          <div class="home-app home-app-md" @click="mdSection">MD编辑</div>
        </div>
      </div>
      <div class="home-content-right-area">
        <input class="home-content-search" placeholder="输入搜索内容" 
          v-model="searchMessageContent"
          @keydown.enter.exact.prevent="searchMessage"></input>
      </div>
    </div>
    <div v-if="toolActive!==''" class="archer-tool">
      <div v-if="toolActive!==''" class="tool-close"><div class="tool-close-btn" @click="toolClose">&times;</div></div>
      <Json v-if="toolActive==='json'"/>
      <MdEditor v-if="toolActive==='mdeditor'"/>
    </div>
  </div>
</template>

<script>
import Json from "./Json.vue"
import MdEditor from './MdEditor.vue'
import time from "@/util/time"


export default {
  name: "Home",
  components: {
    Json,
    MdEditor
  },
  data() {
    return {
      time:{
        currentTime: new Date(),
        isDarkTheme: true
      },
      toolActive: '',
      searchMessageContent: ''
    };
  },
  created() {},
  mounted() {
    let self = this;
    time.schedule(() => {
      self.time.currentTime = new Date()
    }, 1000);
  },
  methods: {
    toggleTheme() {
      this.time.isDarkTheme = !this.time.isDarkTheme;
      document.body.classList.toggle('dark-theme', this.time.isDarkTheme);
    },
    formatDate(date) {
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let dayOfWeek = ['日', '一', '二', '三', '四', '五', '六'][date.getDay()];
      
      return {
        year: `${year}年`,
        month: `${month}月`,
        day: day < 10 ? `0${day}` : `${day}`,
        dayOfWeek: `星期${dayOfWeek}`
      };
    },
        
    formatTime(date) {
      let hours = date.getHours();
      let minutes = date.getMinutes();
      let seconds = date.getSeconds();
      
      return {
        hours: hours < 10 ? `0${hours}` : `${hours}`,
        minutes: minutes < 10 ? `0${minutes}` : `${minutes}`,
        seconds: seconds < 10 ? `0${seconds}` : `${seconds}`
      };
    },
    toolClose() {
      this.toolActive = '';
    },
    jsonSection() {
      this.toolActive = 'json';
    },
    mdSection() {
      this.toolActive = 'mdeditor';
    },
    openUrl(url) {
      window.open(url);
    },
    searchMessage() {
      if(this.searchMessageContent.trim() === '') {
        return
      }
      let content = this.searchMessageContent;
      this.searchMessageContent = ''
      window.open("https://cn.bing.com/search?q="+encodeURI(content))
    }
  },
};
</script>

<style lang="scss" scoped>
.Home {
  padding: 8px;
  display: block;
  width: 100%;
  height: calc(100% - 16px);
  
  .home-title-area {
    display: flex;
    width: calc(100% - 60px);
    height: 40%;
    margin: 20px 30px;
  }

  //时间框
  .home-title-time {
    background: linear-gradient(135deg, #acc6e0 0%, #6893be 100%);
    border-radius: 20px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
    padding: 30px;
    width: 420px;
    max-width: 420px;
    min-width: 420px;
    transition: all 0.3s ease;
  }
  
  .home-title-time:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  }
  
  .date-section {
    display: flex;
    text-align: center;
    margin-bottom: 25px;
    justify-content: center;
    
    .year {
      font-size: 1.2rem;
      color: #6c757d;
      height: 50px;
      line-height: 50px;
    }
    
    .month {
      font-size: 1.8rem;
      font-weight: 600;
      color: #3498db;
      height: 50px;
      line-height: 50px;
    }
    
    .day {
      font-size: 1.8rem;
      font-weight: 600;
      color: #2c3e50;
      height: 50px;
      line-height: 50px;
    }
    
    .day-of-week {
      font-size: 1.2rem;
      font-weight: 500;
      color: #34495e;
      height: 50px;
      line-height: 50px;
    }
  }
  
  .time-section {
    text-align: center;
    background: linear-gradient(45deg, #3498db, #2c3e50);
    border-radius: 15px;
    padding: 20px;
    color: white;

    .time-display {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
    }
    
    .time-unit {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    
    .time-value {
      font-size: 2.8rem;
      font-weight: 700;
      min-width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(255, 255, 255, 0.15);
      border-radius: 10px;
      backdrop-filter: blur(10px);
      transition: all 0.3s ease;
    }
    
    .time-label {
      font-size: 0.8rem;
      text-transform: uppercase;
      margin-top: 5px;
      opacity: 0.7;
    }
    
    .time-separator {
      font-size: 2.5rem;
      font-weight: 700;
      padding: 0 5px;
      align-self: center;
    }
  }
  
  .dark-theme {
    background: linear-gradient(135deg, #2c3e50 0%, #1a2530 100%);
  }
  
  .dark-theme .datetime-card {
    background: linear-gradient(145deg, #34495e, #2c3e50);
    color: #ecf0f1;
  }
  
  .dark-theme .year {
    color: #bdc3c7;
  }
  
  .dark-theme .month {
    color: #3498db;
  }
  
  .dark-theme .day {
    color: #ecf0f1;
  }
  
  .dark-theme .day-of-week {
    color: #bdc3c7;
  }
  //时间框 end

  //右上角个人信息
  .home-title-desc {
    margin-left: 150px;
  }

  .home-content-area {
    display: flex;
    height: 45%;
    margin: 40px 30px;

    .home-content-line {
      margin-top: 20px;
      height: 120px;
      width: 480px;
      display: flex;

      .home-app {
        max-width: 120px;
        min-width: 120px;
        height: 80px;
        line-height: 80px;
        color: #ecf0f1;
        margin: 10px 0px;
        margin-right: 60px;
        border-radius: 7px;
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
        transition: all 0.3s ease;
