    margin-left: auto;
    margin-right: 10px;
  }
  .tool-close-btn:hover {
    color: #ff1818;
    cursor: pointer;
  }
  .Json {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    height: calc(100% - 30px);
  }
  
  .MdEditor {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    height: calc(100% - 30px);
  }
}
</style>
